body {
	margin: 0;
	color: $font-color;
	font-family: $copy-font;
	font-size: 1.1em;
	-webkit-font-smoothing: antialiased;
}
h1,
h2,
h3 {
	font-weight: 600;
	color: $font-color;
	font-family: $heading-font;
	line-height: 1.5;
}
h1 {
	color: $font-color;
	font-size: 2.5em;
	a,
	a:visited {
		text-decoration: none;
		color: $dark-purple;
	}
}
h2 {
	font-size: 2em;
}
a,
a:visited {
	color: $dark-purple;
	text-decoration: none;
	font-weight: bold;
}
a:hover {
	color: $purple;
	text-decoration: none;
}
main {
	padding: 0;
	margin: 0 auto;
}
time {
	color: #898989;
}
.container {
	margin: 0 auto;
}



/* Header */

header#main_header {
	background: $light-orange;
	width: 100%;
	height: 60px;
	div.container {
		div.logo {
			float: left;
			height: 42.5px;
			padding: 7.5px 0px 0px 15px;
			img {
				height: 42.5px;
			}
		}
		nav#main_nav_desktop {
			float: right;
			display: none;
			@media screen and (min-width: 1025px) {
				display: block;
			}
			ul.navigation {
				list-style: none;
				margin: 0;
				li {
					display: inline-block;
					padding: 15px;
					a {
						text-decoration: none;
						.current {
							color: $purple;
						}
					}
				}
			}
		}
	}
}

/* Hamburgesa Icono de Navegación =========================================== */
#nav-icon1 {
  width: 40px;
  height: 40px;
	float: right;
  position: relative;
  margin: 10px 0px 10px 10px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 1000;
	@media screen and (min-width: 1025px) {
		display: none;
	}
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 3px;
  width: 25px;
  background: #282828;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
  z-index: 1000;
}

#nav-icon1 span:nth-child(1) {
  top: 15px;
  left: 7.5px;
}

#nav-icon1 span:nth-child(2) {
  top: 23px;
  left: 7.5px;
}

#nav-icon1.cross span {
  background: #8a1913;
}

#nav-icon1.cross span:nth-child(1) {
  top: 22px;
  left: 7.5px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

#nav-icon1.cross span:nth-child(2) {
  top: 22px;
  left: 7.5px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
/* Final de la Hamburgesa Icono de Navegación =============================== */

/* Dropdown Menu  Navigation================================================= */
div#submenu	{
	width: 100%;
	min-height: 100vh;
	background-color: $dark-purple;
}
.submenu {
	position: absolute;
	visibility: hidden;
	top: -800px;
	transition: top 0.5s,visibility 0.5s, opacity 0.5s linear;
   -moz-transition: top 0.5s, visibility 0.5s, opacity 0.5s linear;
   -webkit-transition: top 0.5s, visibility 0.5s, opacity 0.5s linear;
   z-index: 14;
	 ul.navigation {
		 list-style: none;
		 padding-left: 0;
		 margin-top: 30%;
		 @media screen and (min-width: 568px) {
			margin-top: 10%;
		 }
		 @media screen and (min-width: 768px) {
    	margin-top: 12%;
		 }
		 li {
			 font-size: 2em;
			 text-align: center;
			 transition: all 0.2s;
			 -webkit-transition: all 0.2s;
			 a {
				 color: $light-green;
			 }
		 }
		 a:hover {
				color: $purple;
				transition: all 0.2s;
				-webkit-transition: all 0.2s;
		 }
		 a:active {
			transform: scale(0.9);
			transition: all 0.2s;
			-webkit-transition: all 0.2s;
		 }
	 }
	 div.container {
		h3 {
			color: $light-green;
			text-align: center;
			padding: 20px 0px;
			margin: 0;
		}
		div.social-icons {
			width: 225px;
			margin: 0 auto;
			a {
				text-align: center;
				padding: 0;
				margin: 0 10px;
				width: 50px;
				display: inline-block;
				svg {
					fill: $light-green;
					width: 35px;
				}
				path {
					fill: $light-green;
					width: 35px;
				}
			}
		}
	}
}
.submenu.open {
	position: absolute;
	visibility: visible;
	z-index: 14;
  top: 0px;
	transition: top 0.5s, visibility 0.5s, opacity 0.7s linear;
   -moz-transition: top 0.5s, visibility 0.5s, opacity 0.7s linear;
   -webkit-transition: top 0.5s, visibility 0.5s, opacity 0.7s linear;
}
/* End of Dropdown Menu  Navigation ========================================= */
/* End of header stuff ====================================================== */

