/* Posts ==================================================================== */
div#post_it {
	width: 85%;
	display: block;
	margin: 0 7.5% 15px;
	background: transparent;
	border-radius: 25px;
	border: 15px solid $light-orange;
	vertical-align: top;
	@media screen and (min-width: 600px) {
		width: 44.62%;
		display: inline-block;
		margin: 0 2.5% 15px;
	}
	@media screen and (min-width: 1025px) {
		width: 31.1%;
		margin: 0 1% 15px;
	}
	@media screen and (min-width: 1400px) {
		width: 22.66%;
		margin: 0 1% 15px;
	}
	img {
		border-bottom-left-radius: 25px;
	}
	h2 {
		padding: 15px 15px 0;
		margin: 0;
		color: $dark-purple;
	}
	time {
		padding: 0 15px 15px;
	}
	p {
		padding: 0 15px;
		color: #000000;
	}
	a {
		background: linear-gradient(to right, transparent 50%, $dark-purple 50%);
		background-size: 200% 100%;
		background-position: left bottom;
		border: 1px solid $dark-purple;
		color: $dark-purple;
		padding: 20px;
		display: block;
		text-align: center;
		text-transform: uppercase;
		transition: all 0.2s ease-in-out;
		margin-bottom: 25px;
		font-size: 1.2em;
		border-bottom-left-radius: 25px;
	}
	a:hover {
		background-position: right bottom;
		color: $light-green;
		transition: all 0.2s ease-in-out;
	}
}

/* Posts Homepage */
div#po-po-po-post {
	width: 100%;
	background: $light-green url('../assets/img/geometric-leaves-25.png');
	padding: 30px 0 30px 0;
	img {
		width: 100%;
	}
}
/* End of Posts stuff ======================================================= */



/* Shopping Basket ========================================================== */
div.cart_button {
	float: right;
	padding: 7.5px 10px 10px 10px;
}

.snipcart-add-item {
	width: 160px;
	background: $light-green;
	color: $dark-purple;
	font-size: 0.9em;
	border: none;
	border-radius: 40px;
	line-height: 40px;
	-webkit-transition: all .2s;
	-moz-transition: all .2s;
	-o-transition: all .2s;
	transition: all .2s;
}

.snipcart-add-item:hover {
	@media screen and (min-width: 1025px) {
		background: #85A185;
		color: #252C33;
		transform: scale(1.1);
		-webkit-transition: all .2s;
		-moz-transition: all .2s;
		-o-transition: all .2s;
		transition: all .2s;
	}
}

.snipcart-add-item:active {
		transform: scale(0.9);
		-webkit-transition: all .2s;
		-moz-transition: all .2s;
		-o-transition: all .2s;
		transition: all .2s;
}

div.snip-header {
	background: $dark-purple !important;
	border-bottom: 1px solid $light-green !important;
	.snip-header__title {
		color: $light-green !important
	}
	.snip-header__total {
		background: $dark-purple !important;
		.snip-header__total-label {
			color: $light-green !important;
		}
		#snipcart-amount {
			color: $light-green !important;
		}
	}
}

.snip-layout {
	color: $font-color !important;
	.snip-layout__close {
		color: $light-green !important;
	}
	.snip-table th {
		border-bottom: 1px solid $light-green !important;
	}
	.snip-table__item {
		color: $font-color !important;
	}
	.snip-product__remove {
		color: $purple !important;
	}
	.snip-btn--highlight-green {
		background: $light-green !important;
		color: $dark-purple !important;
		border-radius: 40px !important;
		-webkit-transition: all .2s;
		-moz-transition: all .2s;
		-o-transition: all .2s;
		transition: all .2s;
	}
	.snip-btn--highlight-green:hover {
		@media screen and (min-width: 1025px) {
			background: #85A185 !important;
			color: #252C33 !important;
			transform: scale(1.1) !important;
			-webkit-transition: all .2s;
		  -moz-transition: all .2s;
		  -o-transition: all .2s;
		  transition: all .2s;
		}
	}
	.snip-btn--highlight-green:active {
			transform: scale(0.9) !important;
			-webkit-transition: all .2s;
		  -moz-transition: all .2s;
		  -o-transition: all .2s;
		  transition: all .2s;
	}
	.snip-btn--left {
		background: #fff !important;
		border: 1px solid $light-green !important;
		border-radius: 40px !important;
		color: $dark-purple !important;
	}
	.snip-btn--left:hover {
		@media screen and (min-width: 1025px) {
			border: 1px solid #85A185 !important;
			color: #252C33 !important;
			transform: scale(1.1) !important;
			-webkit-transition: all .2s;
		  -moz-transition: all .2s;
		  -o-transition: all .2s;
		  transition: all .2s;
		}
	}
	.snip-btn--left:active {
			transform: scale(0.9) !important;
			-webkit-transition: all .2s;
		  -moz-transition: all .2s;
		  -o-transition: all .2s;
		  transition: all .2s;
	}
	.snip-footer {
		background: $dark-purple !important;
	}
}

